import React from 'react';
import { Link, useHistory } from 'react-router-dom';
import { useForm } from 'react-hook-form';
import { Form, Input, Button } from 'antd';
import Icon from '@ant-design/icons';
import LoginIcon from 'components/Icons/LoginIcon';

type LoginFormProps = {}

const LoginForm = (props: LoginFormProps) => {
  const history = useHistory();

  function handleSubmit(values: any) {
    console.log(values);
    history.push("/admin");
  }

  return (
    <Form
      className="login-form"
      layout="vertical"
      autoComplete="off"
      onFinish={handleSubmit}>

      <div className="text-center">

        <div className="login-form__header">
          <Icon component={LoginIcon} />
          <h1 className="text-3xl">Đăng nhập</h1>
        </div>

      </div>

      <Form.Item label="Email" name="email">
        <Input type="email" />
      </Form.Item>

      <Form.Item label="Mật khẩu" name="password" className="m-0">
        <Input.Password />
        {/* <Button type="link"> */}
        {/* </Button> */}
      </Form.Item>
      <Link className="block mt-2 mb-6" to="/forgot-password">Quên mật khẩu</Link>

      <Form.Item>
        <Button block type="primary" size="large">Đăng nhập</Button>
      </Form.Item>
    </Form>
  )
}

export default LoginForm;